import { withPrefix } from 'gatsby';
import React from 'react';
import { withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import Paragraphs from '../components/Paragraphs';
import { ContentTitle, MultiBreaker, PageTitle, Wysiwyg } from '../ui/utilities';

const AboutPage = ({ t, ...context }) => {
  const expressionsList = t('about.expressions');
  const expressions = `
    <ul>
      ${expressionsList.map(text => `<li>${text}</li>`).join('')}
    </ul>
  `;
  const description = t('about.description', { expressions });

  const slot1 = (
    <MultiBreaker>
      <img src={withPrefix('assets/img/galerie-expo1.jpg')} alt='' />
      <img src={withPrefix('assets/img/galerie-expo2.jpg')} alt='' />
    </MultiBreaker>
  );

  return (
    <PageWrapper title={t('about.title')} reader {...context}>
      <PageTitle>{t('about.title')}</PageTitle>
      <ContentTitle className='t-h1' aria-hidden='true'>
        {t('about.title')}
      </ContentTitle>
      <Wysiwyg>
        <Paragraphs lines={description} margin components={[ slot1 ]} />
      </Wysiwyg>
    </PageWrapper>
  );
};

export default withIntl()(AboutPage);
